import { memo } from 'react';
import dynamic from 'next/dynamic';
import {
  I_FormCapture,
  I_FormCaptureConfig,
  I_FormCaptureConfigGlobal,
} from '@containers/Home/types';
import { Drawer } from 'antd';
import { DialogFormCaptureWrapper, PanelFormCaptureWrapper } from './styled';

const DialogContainer = dynamic(() => import('@components/DialogContainer'));
const Body = dynamic(() => import('./Body'));

interface Props {
  visible: boolean;
  onCancel: () => void;
  formCaptureSelected?: I_FormCapture;
  formCaptureConfigSelected?: I_FormCaptureConfig;
  formCaptureConfigGlobal?: I_FormCaptureConfigGlobal;
}

const DialogFormCapture = memo(
  ({
    visible,
    formCaptureSelected,
    formCaptureConfigSelected,
    formCaptureConfigGlobal,
    onCancel,
  }: Props) => {
    return formCaptureConfigGlobal?.layout?.type === 'dialog' ? (
      <DialogContainer
        visible={visible}
        onCancel={onCancel}
        maskClosable={formCaptureConfigSelected?.closeable}
        maskStyle={{ backgroundColor: formCaptureConfigGlobal?.overlayColor }}
        top={0}
        width="540px"
        centered
        hiddenCloseButton={!formCaptureConfigSelected?.closeable}
        bodyStyle={{ maxWidth: '540px', width: '100%' }}
        keyboard={formCaptureConfigSelected?.closeable}
      >
        <DialogFormCaptureWrapper
          style={{ backgroundColor: formCaptureConfigGlobal?.backgroundColor }}
        >
          {formCaptureSelected && (
            <Body
              formCaptureSelected={formCaptureSelected}
              onCancel={onCancel}
            />
          )}
        </DialogFormCaptureWrapper>
      </DialogContainer>
    ) : (
      <Drawer
        open={visible}
        placement={
          formCaptureConfigGlobal?.layout?.type === 'panel'
            ? formCaptureConfigGlobal?.layout?.position
            : 'left'
        }
        onClose={onCancel}
        closable={formCaptureConfigSelected?.closeable}
        maskClosable={formCaptureConfigSelected?.closeable}
        maskStyle={{ backgroundColor: formCaptureConfigGlobal?.overlayColor }}
        rootStyle={{ width: '100%' }}
        width="700px"
        bodyStyle={{ padding: 0 }}
        keyboard={formCaptureConfigSelected?.closeable}
      >
        <PanelFormCaptureWrapper
          style={{ backgroundColor: formCaptureConfigGlobal?.backgroundColor }}
        >
          {formCaptureSelected && (
            <Body
              formCaptureSelected={formCaptureSelected}
              onCancel={onCancel}
            />
          )}
        </PanelFormCaptureWrapper>
      </Drawer>
    );
  },
);

export default DialogFormCapture;
